import React, { useState } from 'react';
import { FaMapMarkerAlt, FaPhone, FaEnvelope, FaClock, FaPaperPlane, FaCheck, FaBuilding, FaUsers, FaShieldAlt } from 'react-icons/fa';
import './contact.css';

interface FormData {
  name: string;
  email: string;
  phone: string;
  subject: string;
  message: string;
}

const ContactSection: React.FC = () => {
  const [formData, setFormData] = useState<FormData>({
    name: '',
    email: '',
    phone: '',
    subject: 'General Inquiry',
    message: ''
  });

  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isSubmitted, setIsSubmitted] = useState(false);
  const [errors, setErrors] = useState<Partial<FormData>>({});

  const contactInfo = [
    {
      icon: <FaMapMarkerAlt />,
      title: "Our Office",
      details: ["123 Market Street", "San Francisco, CA 94105"],
      link: "https://maps.google.com/?q=123+Market+Street+San+Francisco+CA",
      color: "#6B1F2B",
      btn: "Get Directions"
    },
    {
      icon: <FaPhone />,
      title: "Call Us",
      details: ["(603) 508-3888"],
      link: "tel:+16035083888",
      color: "#C9A227",
      btn: "Call Now"
    },
    {
      icon: <FaEnvelope />,
      title: "Email Us",
      details: ["sales@marketstreetpropertymanagement.com"],
      link: "mailto:sales@marketstreetpropertymanagement.com",
      color: "#374151",
      btn: "Send Email"
    }
  ];

  const companyStats = [
    {
      icon: <FaBuilding />,
      value: "30+",
      label: "Years Experience",
      description: "Property Management"
    },
    {
      icon: <FaUsers />,
      value: "500+",
      label: "Properties Managed",
      description: "Active Portfolio"
    },
    {
      icon: <FaShieldAlt />,
      value: "99%",
      label: "Tenant Retention",
      description: "Satisfaction Rate"
    }
  ];

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement | HTMLSelectElement>) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
    if (errors[name as keyof FormData]) {
      setErrors(prev => ({
        ...prev,
        [name]: undefined
      }));
    }
  };

  const validateForm = (): boolean => {
    const newErrors: Partial<FormData> = {};
    
    if (!formData.name.trim()) {
      newErrors.name = 'Name is required';
    }
    
    if (!formData.email.trim()) {
      newErrors.email = 'Email is required';
    } else if (!/\S+@\S+\.\S+/.test(formData.email)) {
      newErrors.email = 'Please enter a valid email';
    }
    
    if (!formData.phone.trim()) {
      newErrors.phone = 'Phone is required';
    }
    
    if (!formData.message.trim()) {
      newErrors.message = 'Message is required';
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!validateForm()) {
      return;
    }

    setIsSubmitting(true);
    
    try {
      await new Promise(resolve => setTimeout(resolve, 1500));
      console.log('Form submitted:', formData);
      setIsSubmitted(true);
      
      setTimeout(() => {
        setFormData({
          name: '',
          email: '',
          phone: '',
          subject: 'General Inquiry',
          message: ''
        });
        setIsSubmitted(false);
      }, 5000);
    } catch (error) {
      console.error('Submission error:', error);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <section id="contact" className="contact-section" aria-labelledby="contact-heading">
      <div className="container">
        {/* Section Header */}
        <div className="contact-header">
          <h2 id="contact-heading" className="section-title">
            Get in Touch
          </h2>
          <p className="section-subtitle">
            Ready to experience stress-free property management? Contact our team today.
          </p>
        </div>

        {/* Company Stats */}
        <div className="company-stats">
          {companyStats.map((stat, index) => (
            <div key={index} className="stat-item">
              <div className="stat-icon" aria-hidden="true">
                {stat.icon}
              </div>
              <div className="stat-content">
                <div className="stat-value">{stat.value}</div>
                <div className="stat-label">{stat.label}</div>
                <div className="stat-description">{stat.description}</div>
              </div>
            </div>
          ))}
        </div>

        {/* Main Contact Content */}
        <div className="contact-main-content">
          {/* Left Side - Contact Information */}
          <div className="contact-info-side">
            <div className="info-section">
              <h3 className="info-heading">Contact Information</h3>
              <p className="info-subtitle">Reach out through any of these channels</p>
              
              <div className="contact-details">
                {contactInfo.map((info, index) => (
                  <div key={index} className="contact-detail">
                    <div 
                      className="detail-icon-container"
                      style={{ backgroundColor: info.color }}
                    >
                      <div className="detail-icon" aria-hidden="true">
                        {info.icon}
                      </div>
                    </div>
                    <div className="detail-content">
                      <h4 className="detail-title">{info.title}</h4>
                      <div className="detail-info">
                        {info.details.map((detail, idx) => (
                          <p key={idx} className="detail-text">{detail}</p>
                        ))}
                      </div>
                      {info.link && (
                        <a 
                          href={info.link} 
                          className="detail-link"
                          target="_blank"
                          rel="noopener noreferrer"
                          aria-label={`${info.title} - ${info.details[0]}`}
                        >
                        {info.btn}
                        </a>
                      )}
                    </div>
                  </div>
                ))}
              </div>
            </div>

          
           
          </div>

          {/* Right Side - Contact Form */}
          <div className="contact-form-side">
            <div className="form-wrapper">
              <div className="form-header">
                <h3 className="form-title">Send Us a Message</h3>
                <p className="form-subtitle">We typically respond within 1-2 business hours</p>
              </div>

              {isSubmitted ? (
                <div className="success-message" role="alert" aria-live="polite">
                  <div className="success-icon" aria-hidden="true">
                    <FaCheck />
                  </div>
                  <h4>Thank You!</h4>
                  <p>Your message has been sent successfully. Our team will contact you shortly.</p>
                  <button 
                    className="new-message-btn"
                    onClick={() => setIsSubmitted(false)}
                    aria-label="Send another message"
                  >
                    Send Another Message
                  </button>
                </div>
              ) : (
                <form onSubmit={handleSubmit} className="contact-form" noValidate>
                  <div className="form-row">
                    <div className="form-group">
                      <label htmlFor="name" className="form-label">
                        Full Name *
                      </label>
                      <input
                        type="text"
                        id="name"
                        name="name"
                        value={formData.name}
                        onChange={handleChange}
                        className={`form-input ${errors.name ? 'error' : ''}`}
                        placeholder="John Smith"
                        required
                        aria-required="true"
                        aria-invalid={!!errors.name}
                        aria-describedby={errors.name ? 'name-error' : undefined}
                      />
                      {errors.name && (
                        <span id="name-error" className="error-message" role="alert">
                          {errors.name}
                        </span>
                      )}
                    </div>

                    <div className="form-group">
                      <label htmlFor="email" className="form-label">
                        Email Address *
                      </label>
                      <input
                        type="email"
                        id="email"
                        name="email"
                        value={formData.email}
                        onChange={handleChange}
                        className={`form-input ${errors.email ? 'error' : ''}`}
                        placeholder="john@example.com"
                        required
                        aria-required="true"
                        aria-invalid={!!errors.email}
                        aria-describedby={errors.email ? 'email-error' : undefined}
                      />
                      {errors.email && (
                        <span id="email-error" className="error-message" role="alert">
                          {errors.email}
                        </span>
                      )}
                    </div>
                  </div>

                  <div className="form-row">
                    <div className="form-group">
                      <label htmlFor="phone" className="form-label">
                        Phone Number *
                      </label>
                      <input
                        type="tel"
                        id="phone"
                        name="phone"
                        value={formData.phone}
                        onChange={handleChange}
                        className={`form-input ${errors.phone ? 'error' : ''}`}
                        placeholder="(555) 123-4567"
                        required
                        aria-required="true"
                        aria-invalid={!!errors.phone}
                        aria-describedby={errors.phone ? 'phone-error' : undefined}
                      />
                      {errors.phone && (
                        <span id="phone-error" className="error-message" role="alert">
                          {errors.phone}
                        </span>
                      )}
                    </div>

                    <div className="form-group">
                      <label htmlFor="subject" className="form-label">
                        Subject
                      </label>
                      <select
                        id="subject"
                        name="subject"
                        value={formData.subject}
                        onChange={handleChange}
                        className="form-select"
                      >
                        <option value="General Inquiry">General Inquiry</option>
                        <option value="Property Management">Property Management</option>
                        <option value="Tenant Services">Tenant Services</option>
                        <option value="Maintenance Request">Maintenance Request</option>
                        <option value="Investment Consultation">Investment Consultation</option>
                        <option value="Other">Other</option>
                      </select>
                    </div>
                  </div>

                  <div className="form-group">
                    <label htmlFor="message" className="form-label">
                      Your Message *
                    </label>
                    <textarea
                      id="message"
                      name="message"
                      value={formData.message}
                      onChange={handleChange}
                      className={`form-textarea ${errors.message ? 'error' : ''}`}
                      placeholder="Tell us about your property management needs..."
                      rows={4}
                      required
                      aria-required="true"
                      aria-invalid={!!errors.message}
                      aria-describedby={errors.message ? 'message-error' : undefined}
                    ></textarea>
                    {errors.message && (
                      <span id="message-error" className="error-message" role="alert">
                        {errors.message}
                      </span>
                    )}
                  </div>

                  <button
                    type="submit"
                    className="submit-button"
                    disabled={isSubmitting}
                    aria-label={isSubmitting ? 'Sending message...' : 'Send message'}
                  >
                    {isSubmitting ? (
                      <>
                        <span className="spinner" aria-hidden="true"></span>
                        Sending...
                      </>
                    ) : (
                      <>
                        <FaPaperPlane className="button-icon" aria-hidden="true" />
                        Send Message
                      </>
                    )}
                  </button>

                  <p className="form-disclaimer">
                    By submitting, you agree to our Privacy Policy. We'll never share your information.
                  </p>
                </form>
              )}
            </div>
          </div>
        </div>

        {/* Full Width Map Section */}
        <div className="map-section">
          <h3 className="map-heading">Our Location</h3>
          <div className="map-container">
            <div className="map-wrapper">
              <iframe
                src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d3153.681434336377!2d-122.3999496846822!3d37.774929779759176!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x8085808c4a1e7be9%3A0x9f9e8c5b5e5b5e5b!2sMarket%20Street%2C%20San%20Francisco%2C%20CA!5e0!3m2!1sen!2sus!4v1641234567890!5m2!1sen!2sus"
                width="100%"
                height="100%"
                style={{ border: 0 }}
                allowFullScreen
                loading="lazy"
                title="Property Management Office Location in San Francisco"
                aria-label="Interactive map showing our office location in San Francisco's Market Street"
              ></iframe>
              <div className="map-overlay" aria-hidden="true"></div>
            </div>
            
            <div className="map-info">
              <div className="map-info-item">
                <div className="map-info-icon" aria-hidden="true">
                  <FaMapMarkerAlt />
                </div>
                <div className="map-info-content">
                  <h4>Market Street Property Management</h4>
                  <p>123 Market Street, San Francisco, CA 94105</p>
                  <a 
                    href="https://goo.gl/maps/123" 
                    className="map-directions"
                    target="_blank"
                    rel="noopener noreferrer"
                    aria-label="Get directions to our office on Google Maps"
                  >
                    Get Directions
                  </a>
                </div>
              </div>
              
              <div className="map-info-item">
                <div className="map-info-icon" aria-hidden="true">
                  <FaClock />
                </div>
                <div className="map-info-content">
                  <h4>Visit Us</h4>
                  <p>Monday - Friday: 9:00 AM - 6:00 PM</p>
                  <p>Saturday: 10:00 AM - 4:00 PM</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default ContactSection;